
document.addEventListener('DOMContentLoaded', function(){
  evenlisteners();
 
});

  //textarea
  const textarea= document.querySelector('#textarea');
  // encriptar
  const encriptar= document.querySelector('#encriptar');
  //desencriptar
  const desencriptar= document.querySelector('#desencriptar');
  /// ocultar mensaje inicial
  const mostrarMensaje= document.querySelector('.mostrar__mensaje');
  // btn de copiar
  const copy=document.querySelector('#copy');
  // para insertar las alertas despues de los botones
  const ingresar=document.querySelector('.ingresar__actions');
  // div del texto
  const mostrarText=document.querySelector('.mostrar__text');
  //parrafo de texto
  const mostrar=document.querySelector('#textareaMostrar');

  

  // VARIABLES
  const regex = /^[a-z\s]+$/;

  

function evenlisteners() {
  encriptar.addEventListener('click', encriptarInfo);
  desencriptar.addEventListener('click', desencriptarInfo);
  copy.addEventListener('click', copiar);
}




function encriptarInfo (e){
  

  
  const texto=textarea.value;
  // vaciar el texarea
  textarea.value='';

  if(texto!==""){

    if (regex.test(texto)){
      let resultado = "";
  
      for (let i = 0; i < texto.length; i++) {
        switch (texto[i].toLowerCase()) {
          case "a":
            resultado += "ai";
            break;
          case "e":
            resultado += "enter";
            break;
          case "i":
            resultado += "imes";
            break;
          case "o":
            resultado += "ober";
            break;
          case "u":
            resultado += "ufat";
            break;
          default:
            resultado += texto[i];
        }
      }
    
        
        mostrar.value=resultado;
        mostrarMensaje.style.display='none';
        mostrarText.style.display='flex';
       

    } 
    else 
    {
      mostrarAlerta('error', 'Ingresaste datos inválidos') 
    }

  }
  else 
  {
    mostrarAlerta("error", "Debes ingresar texto");
  }

  

 
  

}


function desencriptarInfo (e){
  const texto=textarea.value;
  // vaciar el texarea
  textarea.value='';

  if(texto!==""){

    if (regex.test(texto)){

    const desencriptado = texto.replace(/ai/g, 'a')
    .replace(/enter/g, 'e')
    .replace(/imes/g, 'i')
    .replace(/ober/g, 'o')
    .replace(/ufat/g, 'u')

      mostrar.value=desencriptado;
      mostrarMensaje.style.display='none';
      mostrarText.style.display='flex';
      
     
    }else{
      mostrarAlerta("error", 'Ingresaste datos inválidos');
    }

  }
  else 
  {
    mostrarAlerta("error", "Debes ingresar texto");
  }
  

  
  

}



function mostrarAlerta (tipo, mensaje){
  limpiarAlertas();
  console.log(mensaje);
  const alerta= document.createElement('P');
  alerta.classList.add('alerta');
  alerta.textContent=mensaje;

  if(tipo==="error"){
    alerta.classList.add('error');
    
  }else {
    alerta.classList.add('correcto');
  }
  ingresar.appendChild(alerta);

  setTimeout(()=>{
    alerta.remove();
  },3000);
}



function copiar(){
  const texto=mostrar.value;
  navigator.clipboard.writeText(texto)

  navigator.clipboard.readText()
  .then(text => {
    mostrarAlerta('correcto', `Guardado en el portapapeles `);
  })
  .catch(err => {
    mostrarAlerta('error', 'no se guardo en el portapapeles ');
  });

  setTimeout(()=>{
    mostrarMensaje.style.display='block';
    mostrarText.style.display='none';
  }, 1000);
  
  
  
}




function limpiarAlertas(){
  const alerta= document.querySelector('.alerta');
  if(alerta){
    alerta.remove();
  }

}



